.Colors {
  margin-bottom: 40px;
  .color {
    display: inline-block;
    position: relative;
    width: 50px;
    height: 50px;
    margin-right: 10px;
    margin-bottom: 10px;
    border: 1px solid black;
    cursor: pointer;

    &:hover {
      .delete-icon-wrapper {
        opacity: 1;
      }
      .darken-layout {
        opacity: 1;
      }
    }
  }

  .delete-icon-wrapper {
    display: inline-block;
    text-align: center;
    width: 100%;
    height: 100%;
    opacity: 0;
    position: relative;
    z-index: 5;
    transition: opacity 0.3s ease;

    svg {
      fill: white;
      width: 100%;
      height: 100%;
    }
  }

  .darken-layout {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    transition: opacity 0.3s ease;
  }
}
