.Images {
  .image-wrapper {

    position: relative;
    width: 100px;
    height: 100px;
    border: 1px solid black;
    vertical-align: middle;

    img {
      width: 100%;
    }
  }
  .image-container {

    display: inline-block;
    margin-right: 10px;
    margin-bottom: 10px;
    cursor: pointer;

    button {
      width: 100%;
      cursor: pointer;
    }
    &:hover {
      .delete-icon-wrapper {
        opacity: 1;
      }
    }
  }

  .delete-icon-wrapper {
    position: absolute;
    opacity: 0;
    transition: opacity 0.3s ease;
    top: 0;
    width: 100%;
    height: 100%;
    svg {
      width: 100%;
      height: 100%;
    }
  }

  .dropzone {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    border-width: 2px;
    border-radius: 2px;
    border-color: #cccccc;
    border-style: dashed;
    background-color: #fafafa;
    color: #676767;
    outline: none;
    transition: border .24s ease-in-out;
    font-family: "Lato", Helvetica, Arial, sans-serif;
    cursor: pointer;
  }
}
