.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.App-header {
  width: 100%;
  height: 80px;
  background: #0b2e13;
}

.App-content {
  flex: 1 0 auto;
}

.App-footer {
  padding: 20px 0;
  flex-shrink: 0;
}

.Paper-main {
  width: 98%;
  max-width: 1200px;
  margin: 20px auto;
  padding: 40px;
  overflow: hidden;
}
